import { createApp } from 'vue'
import './assets/tailwind.css'
import { createRouter,createWebHistory } from "vue-router"

import App from './App.vue'
import HomePage from "./components/HomePage.vue"
import Help from "./components/HelpCentre.vue"
import SellPage from "./components/SellPage.vue"
import LogIn from "./components/LogIn.vue"
import ProductPage from "./components/ProductPage.vue"
import Payment from "./components/Payment.vue"

const router = createRouter({
    history: createWebHistory(""),
    routes:[
        { path:'/thriftee/home' , component: HomePage},
        { path:'/thriftee/' , redirect: HomePage},
        { path:'/thriftee/help' , component: Help},
        { path:'/thriftee/sell' , component: SellPage},
        { path:'/thriftee/login' , component: LogIn},
        { path:'/thriftee/product/:id' , name:'ProductPage', component: ProductPage,props:true},
        { path:'/thriftee/payment/:id' , name:'Payment', component:Payment,props:true}
    ],
});

const app = createApp(App)

app.use(router)

app.mount('#app')
