<template>
  <div class="top">
    <!--Includes all menu, title and other things here -->

    <h1 class="text-2xl font-bold mt-5 2xl:ml-20 md:ml-20 xl:ml-20 ml-10">For you</h1>

    <div class="w-screen flex justify-center">
      <div class="tabs tabs-boxed bg-transparent mt-2 font-extralight hidden md:block xl:block sm:hidden">
        <button
          class="tab btn-sm rounded-md font-normal border-0 capitalize btn-ghost mr-3"
          @click='filter("all")'
        >
          All
        </button>
        <button class=" tab btn-sm rounded-md font-normal capitalize btn-ghost mr-3" @click='filter("men")'>
          Clothing
        </button>
        <button class=" tab btn-sm rounded-md font-normal capitalize btn-ghost mr-3" @click='filter("electronics")'>
          Electronics
        </button>
        <button class=" tab btn-sm rounded-md font-normal capitalize btn-ghost" @click='filter("jewelery")'>
          Jewelery
        </button>
        <button class=" tab btn-sm rounded-md font-normal capitalize btn-ghost" @click='filter("other")'>
          Other
        </button>
      </div>
      
    </div>
  </div>

  <div class="reccomendation w-11/12 ml-auto mr-auto mt-4 columns-1 sm:columns-2 md:columns-3 xl:columns-4 2xl:columns-5">

     <div v-for="items in state.displayElements" :key="items.id" class="mt-8 p-5 hover:shadow-xl hover:rounded-md">

      <div>
        <router-link :to="'/thriftee/product/' + items.id" class=" inline-block w-full h-auto">

          <img :src='items.picture' class="ml-auto mr-auto rounded-md" style="width:150px">
          <h1 class=" text-md font-semibold mt-6">{{items.name}}</h1>
          <p class=" text-blue-400">${{items.price}}</p>


        </router-link>
      </div>

     </div>
  </div>
</template>

<script>
import db from "/hobby/web-dev/selflearn/vue/secshop/src/db"
import { reactive , onMounted } from "vue"

export default {
  name: "HomePage",
  setup(){
    
    const state = reactive({
      elements: [],
      displayElements:[]
    });

    onMounted(() => {
      const datbase = db.database().ref("store")

      datbase.on("value",(snapshot) => {
        const data = snapshot.val()

        const element = []
        Object.keys(data).forEach((key) => {
          element.push({
            category:data[key].category,
            description:data[key].description,
            id:data[key].id,
            name:data[key].name,
            picture:data[key].picture,
            price:data[key].price
          })
        })

        state.elements = element
        state.displayElements = element

        
        for (let i = state.displayElements.length - 1; i > 0; i--) {
           const j = Math.floor(Math.random() * (i + 1));
          [state.displayElements[i], state.displayElements[j]] = [state.displayElements[j], state.displayElements[i]];
         }
      })
    })

    const filter = function filter(category){

        if(category == "all"){
            state.displayElements = state.elements
            return
        }


      state.displayElements = []

      state.elements.forEach((data) => {
          if (data.category.indexOf(category) >= 0){
            state.displayElements.push(data)
          }
      },
      
      )}

    return{
      state,
      filter
    }
  }
};
</script>

