<template>
  <div v-if="done" class="alert alert-success w-4/5 ml-auto mr-auto mt-24 shadow-lg">
    <div>
      <svg
        xmlns="http://www.w3.org/2000/svg"
        class="stroke-current flex-shrink-0 h-6 w-6"
        fill="none"
        viewBox="0 0 24 24"
      >
        <path
          stroke-linecap="round"
          stroke-linejoin="round"
          stroke-width="2"
          d="M9 12l2 2 4-4m6 2a9 9 0 11-18 0 9 9 0 0118 0z"
        />
      </svg>
      <span>Your product had been uploaded!</span>
    </div>
  </div>

  <div v-if="!done" class="h-5/6 w-screen flex justify-center items-center">
    <form @submit.prevent="oclick">
      <div
        class="w-96 p-8 rounded-md shadow-none bordered border-0 xl:border-2 md:border-2 md:shadow-xl xl:shadow-xl 2xl:border-2 bg-white"
      >
        <h1 class="text-2xl font-bold">Sell</h1>

        <label class="mt-3 text-grey-200 float-left">Title</label>
        <input
          class="input input-bordered w-full mb-2"
          placeholder="title"
          type="text"
          v-model="title"
        />

        <label class="mt-3 text-grey-200 float-left">Image Link</label>
        <input
          class="input input-bordered w-full mb-2"
          placeholder="image link"
          type="text"
          v-model="image"
        />

        <label class="text-grey-200">Description</label>
        <textarea
          class="textarea-bordered rounded w-full border-2 mb-2"
          placeholder="  description"
          v-model="description"
        />

        <label class="text-grey-200 float-left w-72">Price</label>
        <input
          class="input input-bordered float-left w-full mb-2"
          placeholder="price"
          type="number"
          v-model="price"
        />
        <br />
        <input
          type="submit"
          class="btn bg-orange-400 text-white border-0 capitalize btn-sm font-normal"
          value="done"
        />
      </div>
    </form>
  </div>
</template>

<script>
import db from "/hobby/web-dev/selflearn/vue/secshop/src/db";
import { ref } from "vue";

export default {
  name: "SellPage",
  setup() {
    const title = ref("");
    const description = ref("");
    const image = ref("");
    const price = ref("");
    const done = ref("")
    done.value = false

    const datbase = db.database().ref("store");

    const oclick = () => {
      datbase.push({
        id: Math.round(Math.random() * 100000),
        name: title.value,
        price: price.value,
        picture: image.value,
        category: "other",
        description: description.value,
      });
      done.value = true
      console.log(done)
    };

    return {
      oclick,
      title,
      description,
      image,
      price,
      done
    };
  },
};
</script>

<style></style>
