<template>
  <div class="block w-11/12 ml-auto mr-auto mt-10 sm:flex sm:w-4/5 md:flex md:w-4/5 xl:flex xl:w-4/5 2xl:flex 2xl:w-4/5">
    <div class="w-full sm:w-2/6 md:w-2/6 xl:w-2/6 2xl:w-2/6 img inline-block">
      <img :src="state.product.picture" class="w-full rounded-md">
    </div>
    <div class="w-full sm:w-5/6 md:w-5/6 xl:w-5/6 2xl:w-5/6 info pl-0 pt-5 md:pl-8 sm:pl-8 xl:pl-8 2xl:pl-8">
      <h1 class="text-2xl font-bold">{{state.product.name}}</h1>
      <p class="text-md font-normal text-blue-500">${{state.product.price}}</p>
      <p class="text-md font-normal">{{state.product.description}}</p>

      <h1 class=" text-md mt-5">Quantity</h1>
      <input class="input input-sm bordered border-2 border-gray-200" type="number">

      <h1 class=" text-md mt-3">Type</h1>
      <select class=" w-48 rounded-md h-8 bordered border-2">
        <option>Red</option>
        <option>Green</option>
        <option>Blue</option>
        <option>Yellow</option>
      </select>
      <br>
      <router-link :to="'/thriftee/payment/' + state.product.id">
          <input type="submit" class="btn mt-5 w-40 btn-sm bg-orange-400 border-0 text-white mb-12" value="buy" @click="love">
      </router-link>
    </div>
  </div>
</template>

<script>
import db from "/hobby/web-dev/selflearn/vue/secshop/src/db"
import {onMounted, reactive} from "vue"

export default {
    name:"ProductPage",
    props:{
      id:String,
      },
    setup(props){
      console.log(props.id)

      const state = reactive({
        product:"",
      });

      const datbase = db.database().ref("store")

      onMounted(()=>{
            datbase.on("value",(snapshot) => {
               const data = snapshot.val()
               var element = ""

                Object.keys(data).forEach((key) => {
                if (data[key].id == props.id){
                   element = data[key]
               }})

  
               state.product = element
            })
      })

      return{
        state
      }
    }
    }
</script>

