<template>
  <div class="w-screen h-5/6 flex items-center justify-center">
      <div class=" w-full p-6 rounded-md shadow-none inline-block bordered border-0 md-5 sm:w-4/5 md:w-3/5 xl:w-4/12 2xl:w-4/12 xl:border-2 md:border-2 md:shadow-xl xl:shadow-xl 2xl:border-2 bg-white">
      <form>
        <h1 class="text-md font-bold">Step {{stepNum}}</h1>

        <div v-if="stepNum == 1">
            <h1 class="text-2xl font-bold">Confirm your location and item</h1>
            <h1 class="text-md font-bold mt-4">Location</h1>
            <input class="input w-full input-bordered mt-2 md-5" placeholder="location" type="text"/>

            <h1 class="text-md font-bold mt-4">Your item</h1>

            <div class="card card-side flex bg-base-100 shadow-none">
              <div class="card-body">
                  <h2 class="card-title">{{state.product.name}}</h2>
                  <p class="text-blue-400">{{state.product.price}}</p>
              </div>
            </div>
        </div>

        <div v-if="stepNum == 2">
            <h1 class="text-2xl font-bold">Choose your payment method</h1>
            <select class=" w-full mt-3 rounded-md h-12 bordered border-2">
                <option>Paypal</option>
                <option>Credit card</option>
                <option>Cash</option>
            </select>
        </div>

        <div v-if="stepNum == 3">
            <h1 class=" text-2xl font-bold">Your order has been received and is now in the queue to be shipped out. </h1>
        </div>

        <button v-if="stepNum <= 2" class="mt-5 btn bg-orange-400 border-0 text-white" @click.prevent="add">next</button>
        </form>
      </div>
  </div>
</template>

<script>
import db from "/hobby/web-dev/selflearn/vue/secshop/src/db"
import {onMounted, ref, reactive} from "vue"

export default {
    name:"ProductPage",
    props:{
      id:String,
      },
    setup(props){
      const stepNum = ref(1)

      const state = reactive({
        product:"",
      });

      const datbase = db.database().ref("store")

      onMounted(()=>{
            datbase.on("value",(snapshot) => {
               const data = snapshot.val()
               var element = ""

                Object.keys(data).forEach((key) => {
                if (data[key].id == props.id){
                   element = data[key]
               }})

  
               state.product = element
            })
      })

      const add = () => {
        stepNum.value += 1
      }

      return{
        state,
        stepNum,
        add
      }
    }
    }
</script>

